<template>
    <div>
        <div class="group" v-bind:class="{selected: group.selected}">
            <div class="group__header">
                <div></div>
                <span class="group__title"
                      @click="openDialogPanel({method: 'changeTitle', columnId, groupId: group.id, place: 'group', title: group.title})"
                >
                    {{group.title}}
                </span>

                <font-awesome-icon
                        icon="minus"
                        class="group__hide"
                        @click="hideContent(columnId, group.id)"
                />
            </div>

            <div v-if="!group.hide">
                <div class="group__main">
                    <div v-for="task in group.tasks">
                        <div v-if="task.type === 'task'">
                            <Task class="group__task" v-bind:task="task" v-bind:columnId="columnId"></Task>
                        </div>
                        <div v-if="task.type === 'group'">
                            <group class="group__task" v-bind:group="task" v-bind:columnId="columnId"></group>
                        </div>
                    </div>
                </div>

                <div class="group__footer">
                    <font-awesome-icon
                            icon="plus"
                            class="btn btn--add"
                            @click="openDialogPanel({method: 'addTask', columnId, groupId: group.id, place: 'group'})"
                    />
                    <font-awesome-icon
                            icon="trash"
                            class="btn btn--del"
                            @click="openDialogPanel({method: 'deleteGroup', columnId, groupId: group.id})"
                    />
                    <font-awesome-icon
                            icon="folder-plus"
                            class="btn btn--group"
                            @click="openDialogPanel({method: 'addGroup', columnId, groupId: group.id, place: 'group'})"
                    />
                    <font-awesome-icon
                            icon="folder-minus"
                            class="btn btn--ungroup"
                            @click="openDialogPanel({method: 'ungroup', columnId, groupId: group.id, place: 'group'})"
                    />
                </div>
            </div>

        </div>
    </div>
</template>

<script>
    // @ is an alias to /src
    import Column from '@/components/Column.vue'
    import Group from '../components/Group.vue'
    import Task from '../components/Task'

    export default {
        name: 'group',
        components: {
            Column,
            Group,
            Task
        },
        props: ['group', 'columnId'],
        data() {
            return {
                selected: []
            }
        },
        methods: {
            openDialogPanel(payload) {
                if (payload.method === 'addTask') {
                    this.$store.dispatch('openDialogPanel', {type: 'addTask', groupId: payload.groupId, columnId: payload.columnId, place: payload.place});
                } else if (payload.method === 'deleteGroup') {
                    this.$store.dispatch('openDialogPanel', {type: 'deleteGroup', groupId: payload.groupId, columnId: payload.columnId, place: payload.place});
                } else if (payload.method === 'addGroup') {
                    this.$store.dispatch('openDialogPanel', {type: 'addGroup', groupId: payload.groupId, columnId: payload.columnId, place: payload.place});
                } else if (payload.method === 'ungroup') {
                    this.$store.dispatch('openDialogPanel', {type: 'ungroup', groupId: payload.groupId, columnId: payload.columnId});
                } else if (payload.method === 'changeTitle') {
                    this.$store.dispatch('openDialogPanel', {type: 'changeTitle', groupId: payload.groupId, columnId: payload.columnId, place: payload.place, title: payload.title});
                }
            },
            hideContent(columnId, groupId) {
                this.$store.dispatch('hideContent', {columnId, groupId})
            },
            addTask(columnId, groupId) {
                this.$store.dispatch('addTask', {columnId, place: 'group', groupId})
            },
            addGroup(columnId, groupId) {
                this.$store.dispatch('addGroup', {columnId, place: 'group', groupId});
            },
            deleteGroup(columnId, groupId) {
                this.$store.dispatch('deleteGroup', {columnId, groupId});
            },
            ungroup(columnId, groupId) {
                this.$store.dispatch('ungroup', {columnId, groupId})
            }
        },
        computed: {}
    }
</script>

<style scoped lang="scss">
    .selected {
        background-color: #bdccf1 !important;
    }
    .group {
        color: black;
        border: 1px solid #999997;
        border-radius: 4px;
        background-color: #dce2d6;

        .group__header {
            display: flex;
            justify-content: space-between;
            background-color: #074d42;
            color: white;
            border-top-left-radius: 4px;
            border-top-right-radius: 4px;
            padding: 0.3em;
            .group__title {
                cursor: pointer;
            }
            .group__hide {
                cursor: pointer;
                margin-right: 3px;
            }
        }

        .group__main {
            text-align: left;
            padding: 0.5em;
            .group__task {
                margin-bottom: 0.5em;
            }
            .task--selected {
                background-color: lightblue;
            }
        }

        .group__footer {
            display: flex;
            justify-content: space-between;
            flex-basis: 100%;

            .btn {
                flex: auto;
                /*width: 2em;*/
                color: #e2efff;
                padding: 0.5em 0.2em;
                font-size: 16px;
                transition: 1s;
                cursor: pointer;
            }
            .btn--add {
                background-color: #075b4d;
                border-bottom-left-radius: 3px;
                &:hover {
                    background-color: #0ca88e;
                }
            }
            .btn--del {
                background-color: #6c1221;
                &:hover {
                    background-color: #c33348;
                }
            }
            .btn--group {
                background-color: #15036c;
                &:hover {
                    background-color: #7b54a9;
                }
            }
            .btn--ungroup {
                background-color: #6c1221;
                border-bottom-right-radius: 3px;
                &:hover {
                    background-color: #c44058;
                }
            }
        }
    }
</style>
