<template>
    <div>
        <div class="column">
            <div class="column__header">
                <span @click="openDialogPanel({method: 'changeTitle', columnId: column.id, place: 'column', title: column.title})"
                      class="column__title"
                >
                    {{column.title}}
                </span>
            </div>

            <div class="column__main">
                <div v-for="task in column.tasks" v-bind:key="task.id">
                    <div v-if="task.type === 'task'">
                        <Task class="task-item" v-bind:task="task" v-bind:columnId="column.id"></Task>
                    </div>
                    <div v-if="task.type === 'group'">
                        <Group class="task-item" v-bind:group="task" v-bind:columnId="column.id"></Group>
                    </div>
                </div>
            </div>

            <div class="column__footer">
                <font-awesome-icon
                        icon="plus"
                        class="btn btn--group"
                        @click="openDialogPanel({method: 'addTask', columnId: column.id, place: 'column'})"
                />
                <font-awesome-icon
                        icon="trash"
                        class="btn btn--group"
                        @click="openDialogPanel({method: 'deleteColumn', columnId: column.id})"
                />
                <font-awesome-icon
                        icon="folder-plus"
                        class="btn btn--group"
                        @click="openDialogPanel({method: 'addGroup', columnId: column.id, place: 'column'})"
                />
            </div>

        </div>
    </div>
</template>

<script lang="js">
    import Group from './Group'
    import Task from './Task'
    export default {
        name: 'Column',
        props: ['column'],
        components: {
            Group,
            Task
        },
        data () {
            return {
                isEdit: false,
                edited: ' '
            }
        },
        methods: {
            openDialogPanel(payload) {
                if (payload.method === 'addTask') {
                    this.$store.dispatch('openDialogPanel', {type: payload.method, columnId: payload.columnId, place: payload.place});
                } else if (payload.method === 'deleteColumn') {
                    this.$store.dispatch('openDialogPanel', {type: payload.method, columnId: payload.columnId});
                } else if (payload.method === 'addGroup') {
                    this.$store.dispatch('openDialogPanel', {type: payload.method, columnId: payload.columnId, place: payload.place});
                } else if (payload.method === 'changeTitle') {
                    this.$store.dispatch('openDialogPanel', {type: payload.method, columnId: payload.columnId, place: payload.place, title: payload.title});
                }
            },
            addTask(columnId, place) {
                this.$store.dispatch('addTask', {columnId, place})
            },
            deleteColumn(columnId) {
                this.$store.dispatch('deleteColumn', {columnId});
            },
            addGroup(columnId) {
                this.$store.dispatch('addGroup', {columnId, place: 'column'});
            }
        }
    }
</script>

