export default {
    state: {
        isOpen: false,
        payload: {}
    },
    mutations: {
        openDialogPanel(state, payload) {
            state.isOpen = true;
            state.payload = {
                type: payload.type,
                columnId: payload.columnId,
                groupId: payload.groupId,
                taskId: payload.taskId,
                place: payload.place,
                title: payload.title,
            };
        },
        closeDialogPanel(state) {
            state.isOpen = false;
        }
    },
    actions: {
        openDialogPanel({commit}, payload) {
            commit('openDialogPanel', payload);
        },
        closeDialogPanel({commit}) {
            commit('closeDialogPanel');
        }
    },
    getters: {
        isOpen(state) {
            return state.isOpen
        },
        getType(state) {
            return state.payload.type
        },
        getData(state) {
            if (state.payload.type === 'addTask') {
                return {columnId: state.payload.columnId, groupId: state.payload.groupId, place: state.payload.place}
            } else if (state.payload.type === 'deleteColumn') {
                return {columnId: state.payload.columnId}
            } else if (state.payload.type === 'deleteGroup') {
                return {columnId: state.payload.columnId, groupId: state.payload.groupId, place: state.payload.place}
            } else if (state.payload.type === 'addGroup') {
                return {columnId: state.payload.columnId, groupId: state.payload.groupId, place: state.payload.place}
            } else if (state.payload.type === 'ungroup') {
                return {columnId: state.payload.columnId, groupId: state.payload.groupId}
            } else if (state.payload.type === 'changeTitle') {
                return {columnId: state.payload.columnId, groupId: state.payload.groupId, place: state.payload.place, title: state.payload.title}
            } else if (state.payload.type === 'editTask') {
                return {columnId: state.payload.columnId, taskId: state.payload.taskId}
            } else if (state.payload.type === 'deleteTask') {
                return {columnId: state.payload.columnId, taskId: state.payload.taskId}
            }
        }
    }
}

