<template>
    <div>
        <div class="panel">
            <div class="panel__header">
                <h1 class="title">{{viewElements.title}}</h1>
            </div>

            <div class="panel__main">
                <div class="panel__field">
                    <label class="field__title">{{viewElements.description}}</label>
                    <input v-if="viewElements.singleLineInput" class="field__input" name="msg" v-model="singleLineInput" autocomplete="off"/>
                    <textarea v-if="viewElements.multiLineInput" name="msg" cols="25" rows="3" v-model="multiLineInput"></textarea>
                </div>
                <div class="panel__buttons">
                    <button
                            class="button"
                            @click="viewElements.cancel"
                    >
                        
                    </button>
                    <button
                            class="button"
                            @click="viewElements.ok"
                    >
                        
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data () {
            return {
                type: this.$store.getters.getType,
                singleLineInput: '',
                multiLineInput: '',
                viewElements: this.setValue()
            }
        },
        methods: {
            closeDialogPanel() {
                this.$store.dispatch('closeDialogPanel');
            },
            addTask() {
                let columnId = this.$store.getters.getData.columnId;
                let groupId = this.$store.getters.getData.groupId;
                let place = this.$store.getters.getData.place;
                let text = this.multiLineInput;
                if (text) {
                    this.$store.dispatch('addTask', {columnId, groupId, place, text});
                    this.$store.dispatch('closeDialogPanel');
                }
            },
            deleteColumn() {
                let columnId = this.$store.getters.getData.columnId;
                this.$store.dispatch('deleteColumn', {columnId});
                this.$store.dispatch('closeDialogPanel');
            },
            addGroup() {
                let columnId = this.$store.getters.getData.columnId;
                let groupId = this.$store.getters.getData.groupId;
                let place = this.$store.getters.getData.place;
                let text = this.singleLineInput;
                if (text) {
                    this.$store.dispatch('addGroup', {columnId, groupId, place, text});
                    this.$store.dispatch('closeDialogPanel');
                }
            },
            deleteGroup() {
                let columnId = this.$store.getters.getData.columnId;
                let groupId = this.$store.getters.getData.groupId;
                this.$store.dispatch('deleteGroup', {columnId, groupId});
                this.$store.dispatch('closeDialogPanel');
            },
            ungroup() {
                let columnId = this.$store.getters.getData.columnId;
                let groupId = this.$store.getters.getData.groupId;
                this.$store.dispatch('ungroup', {columnId, groupId});
                this.$store.dispatch('closeDialogPanel');
            },
            changeTitle() {
                let columnId = this.$store.getters.getData.columnId;
                let groupId = this.$store.getters.getData.groupId;
                let place = this.$store.getters.getData.place;
                let text = this.singleLineInput;
                if (text) {
                    this.$store.dispatch('changeTitle', {columnId, groupId, place, text});
                    this.$store.dispatch('closeDialogPanel');
                }
            },
            editTask() {
                let columnId = this.$store.getters.getData.columnId;
                let text = this.multiLineInput;
                let taskId = this.$store.getters.getData.taskId;
                if (text) {
                    this.$store.dispatch('editTask', {columnId, taskId, text});
                    this.$store.dispatch('closeDialogPanel');
                }
            },
            deleteTask() {
                let columnId = this.$store.getters.getData.columnId;
                let taskId = this.$store.getters.getData.taskId;
                this.$store.dispatch('deleteTask', {columnId, taskId});
                this.$store.dispatch('closeDialogPanel');
            },

            setValue() {
                let type = this.$store.getters.getType;
                if (type === 'addTask') {
                    return {
                        title: ' ',
                        description: ' ',
                        singleLineInput: false,
                        multiLineInput: true,
                        cancel: this.closeDialogPanel,
                        ok: this.addTask
                    }
                }
                if (type === 'deleteColumn') {
                    return {
                        title: ' ',
                        description: '     ?',
                        singleLineInput: false,
                        multiLineInput: false,
                        cancel: this.closeDialogPanel,
                        ok: this.deleteColumn
                    }
                }
                if (type === 'addGroup') {
                    return {
                        title: '  ',
                        description: '  :',
                        singleLineInput: true,
                        multiLineInput: false,
                        cancel: this.closeDialogPanel,
                        ok: this.addGroup
                    }
                }
                if (type === 'deleteGroup') {
                    return {
                        title: '  ',
                        description: '      ?',
                        singleLineInput: false,
                        multiLineInput: false,
                        cancel: this.closeDialogPanel,
                        ok: this.deleteGroup
                    }
                }
                if (type === 'ungroup') {
                    return {
                        title: '',
                        description: '      ?',
                        singleLineInput: false,
                        multiLineInput: false,
                        cancel: this.closeDialogPanel,
                        ok: this.ungroup
                    }
                }
                if (type === 'changeTitle') {
                    return {
                        title: ' ',
                        description: '  :',
                        singleLineInput: true,
                        multiLineInput: false,
                        cancel: this.closeDialogPanel,
                        ok: this.changeTitle
                    }
                }
                if (type === 'editTask') {
                    return {
                        title: ' ',
                        description: '  :',
                        singleLineInput: false,
                        multiLineInput: true,
                        cancel: this.closeDialogPanel,
                        ok: this.editTask
                    }
                }
                if (type === 'deleteTask') {
                    return {
                        title: ' ',
                        description: '     ?',
                        singleLineInput: false,
                        multiLineInput: false,
                        cancel: this.closeDialogPanel,
                        ok: this.deleteTask
                    }
                }
            }
        },
        computed: {}
    }
</script>

