let doSomething = function(payload, method) {
    this.tasks.forEach((task, i) => {
        if (task.id === payload.id) {
            method === 'hide'          ?   hideGroup(task) : null;
            method === 'addTask'       ?   addTask(task, payload.text) : null;
            method === 'addGroup'      ?   addGroup(task, payload.text) : null;
            method === 'ungroup'       ?   ungroup(task, i, this) : null;
            method === 'deleteGroup'   ?   deleteGroup(this, i) : null;
            method === 'changeTitle'   ?   changeTitle(this.tasks[i], payload.text) : null;
            method === 'editTask'      ?   editTask(task, payload.text) : null;
            method === 'deleteTask'    ?   deleteTask(this, i) : null;
        } else {
            task.type === 'group' ? task.doSomething(payload, method) : null
        }
    });
};

let hideGroup = function(task) {
    task.hide = !task.hide;
};

let addTask = function(task, text) {
    task.tasks.push(newTask(text));
};

let addGroup = function(task, text) {
    task.tasks.push(newGroup(text))
};

let deleteColumn = function(context, i) {
    context.splice(i, 1);
};

let changeTitle = function(context, text) {
    context.title = text;
};

let deleteGroup = function(context, i) {
    context.tasks.splice(i, 1);
};

let ungroup = function(task, i, context) {
    context.tasks.splice(i, 1, ...task.tasks);
};

let editTask = function(context, text) {
    context.text = text;
};

let deleteTask = function(context, i) {
    context.tasks.splice(i, 1);
};


let newTask = function(text) {
    return {type: 'task', text: text, id: Math.random()*100000};
};

let newGroup = function(text) {
    return {
        type: 'group', title: text, selected: false, hide: false, tasks: [], id: Math.random() * 1000000,
        doSomething: doSomething
    }
};

export default {
    state: {
        columnList: [
            {
                id: 0,
                title: '',
                tasks: [
                    {type: 'group', title: '  1', selected: false, hide: false, tasks: [
                            {type: 'task', text: ' 1', selected: false, id: 4},
                            {type: 'task', text: ' 2', selected: false, id: 5},
                        ],
                        id: 3,
                        doSomething: doSomething
                    },
                    {type: 'group', title: '  2', selected: false, hide: false, tasks: [
                            {type: 'task', text: ' 1', selected: false, id: 11},
                            {type: 'task', text: ' 2', selected: false, id: 12},
                            {type: 'group', title: '  1', selected: false, hide: false, tasks: [
                                    {type: 'task', text: ' 1', selected: false, id: 13},
                                    {type: 'task', text: ' 2', selected: false, id: 14},
                                ], id: 10,
                                doSomething: doSomething
                            }
                        ], id: 9,
                        doSomething: doSomething
                    },
                ]
            },
            {
                id: 101,
                title: ' ',
                tasks: [
                    {type: 'group', title: '  1', selected: false, hide: false, tasks: [
                            {type: 'task', text: ' 4', selected: false, id: 16},
                            {type: 'group', title: '  1', selected: false, hide: false, tasks: [
                                    {type: 'task', text: ' 14', selected: false, id: 17},
                                    {type: 'task', text: ' 15', selected: false, id: 18},
                                ], id: 19,
                                doSomething: doSomething
                            },
                            {type: 'task', text: ' 13', selected: false, id: 20},
                        ], id: 21,
                        doSomething: doSomething
                    },
                ]
            },
            {
                id: 102,
                title: '',
                tasks: [
                    {type: 'task', text: ' 1', selected: false, id: 719}
                ]
            }
        ]
    },
    mutations: {
        hideContent(state, payload) {
            state.columnList.filter((column) => {
                column.id === payload.columnId ? (
                    column.tasks.forEach((task) => {
                        if (task.id === payload.groupId) {
                            hideGroup(task)
                        } else {
                            task.type === 'group' ? task.doSomething({id: payload.groupId}, 'hide') : null
                        }
                    })
                ) : null
            });
        },
        changeTitle(state, payload) {
            state.columnList.filter((column) => {
                column.id === payload.columnId ? (
                    payload.place === 'column' ? (
                        changeTitle(column, payload.text)
                    ) : (
                        column.tasks.forEach((task) => {
                            if (task.id === payload.groupId) {
                                changeTitle(task, payload.text)
                            } else {
                                task.type === 'group' ? task.doSomething({id: payload.groupId, text: payload.text}, 'changeTitle') : null
                            }
                        })
                    )
                ) : null
            });
        },
        addTask(state, payload) {
            state.columnList.filter((column) => {
                column.id === payload.columnId ? (
                    payload.place === 'column' ? (
                            addTask(column, payload.text)
                        ) : (
                            column.tasks.forEach((task) => {
                                if (task.id === payload.groupId) {
                                    addTask(task, payload.text)
                                } else {
                                    task.type === 'group' ? task.doSomething({id: payload.groupId, text: payload.text}, 'addTask') : null
                                }
                            })
                        )
                ) : null
            });
        },
        deleteColumn(state, payload) {
            state.columnList.filter((column, i) => {
                column.id === payload.columnId ? (
                    deleteColumn(state.columnList, i)
                ) : null
            });
        },
        addGroup(state, payload) {
            state.columnList.filter((column) => {
                column.id === payload.columnId ? (
                    payload.place === 'column' ? (
                        addGroup(column, payload.text)
                    ) : (
                        column.tasks.forEach((task) => {
                            if (task.id === payload.groupId) {
                                addGroup(task, payload.text)
                            } else {
                                task.type === 'group' ? task.doSomething({id: payload.groupId, text: payload.text}, 'addGroup') : null
                            }
                        })
                    )
                ) : null
            });
        },
        deleteGroup(state, payload) {
            state.columnList.filter((column) => {
                column.id === payload.columnId ? (
                    column.tasks.forEach((task, i) => {
                        if (task.id === payload.groupId) {
                            deleteGroup(column, i);
                        } else {
                            task.type === 'group' ? task.doSomething({id: payload.groupId}, 'deleteGroup') : null
                        }
                    })
                ) : null
            });
        },
        ungroup(state, payload) {
            state.columnList.filter((column) => {
                column.id === payload.columnId ? (
                    column.tasks.forEach((task, i) => {
                        if (task.id === payload.groupId) {
                            ungroup(task, i, column);
                        } else {
                            task.type === 'group' ? task.doSomething({id: payload.groupId}, 'ungroup') : null
                        }
                    })
                ) : null
            });
        },
        editTask(state, payload) {
            state.columnList.filter((column) => {
                column.id === payload.columnId ? (
                    column.tasks.forEach((task) => {
                        if (task.id === payload.taskId) {
                            editTask(task, payload.text)
                        } else {
                            task.type === 'group' ? task.doSomething({id: payload.taskId, text: payload.text}, 'editTask') : null
                        }
                    })
                ) : null
            });
        },
        deleteTask(state, payload) {
            state.columnList.filter((column) => {
                column.id === payload.columnId ? (
                    column.tasks.forEach((task, i) => {
                        if (task.id === payload.taskId) {
                            deleteTask(column, i)
                        } else {
                            task.type === 'group' ? task.doSomething({id: payload.taskId}, 'deleteTask') : null
                        }
                    })
                ) : null
            });
        },
    },

    actions: {
        select() {},

        addTask({commit}, payload) {
            commit('addTask', payload);
        },
        addGroup({commit}, payload) {
            commit('addGroup', payload);
        },
        hideContent({commit}, payload) {
            commit('hideContent', payload);
        },
        deleteColumn({commit}, payload) {
            commit('deleteColumn', payload);
        },
        changeTitle({commit}, payload) {
            commit('changeTitle', payload);
        },
        deleteGroup({commit}, payload) {
            commit('deleteGroup', payload);
        },
        ungroup({commit}, payload) {
            commit('ungroup', payload)
        },
        editTask({commit}, payload) {
            commit('editTask', payload)
        },
        deleteTask({commit}, payload) {
            commit('deleteTask', payload)
        }
    },
    getters: {
        columns(state){
            return state.columnList
        }
    }
}
