<template>
    <div>
        <div class="task" v-bind:class="{selected: task.selected}">

            <div class="task__main">
                <span>{{task.text}}</span>
                <font-awesome-icon
                        icon="cog"
                        class="btn btn--setting"
                        @click="isHideMenu = !isHideMenu"
                />
            </div>

            <div class="task__footer" v-if="!isHideMenu">
                <font-awesome-icon
                        icon="edit"
                        class="btn btn--edit"
                        @click="openDialogPanel({method: 'editTask', columnId: columnId, taskId: task.id})"
                />
                <font-awesome-icon
                        icon="trash"
                        class="btn btn--delete"
                        @click="openDialogPanel({method: 'deleteTask', columnId: columnId, taskId: task.id})"
                />
            </div>

        </div>
    </div>
</template>

<script>
    export default {
        props: ['columnId', 'task'],
        data() {
            return {
                isHideMenu: true
            }
        },
        methods: {
            openDialogPanel(payload) {
                if (payload.method === 'editTask') {
                    this.$store.dispatch('openDialogPanel', {type: 'editTask', columnId: payload.columnId, taskId: payload.taskId});
                } else if (payload.method === 'deleteTask') {
                    this.$store.dispatch('openDialogPanel', {type: 'deleteTask', columnId: payload.columnId, taskId: payload.taskId});
                }
            }
        },
        computed: {}
    }
</script>

